@import url('https://fonts.googleapis.com/css2?family=League+Spartan:wght@700&display=swap');

/* width */
::-webkit-scrollbar {
  width: 10px;
}

/* Track */
::-webkit-scrollbar-track {
  background: #f1f1f113;
  border-radius: 5px;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: #888888;
  border-radius: 5px;
}
.bg-img{
  position: absolute;
background-image: url('./play.png');
background-repeat: no-repeat;
background-position-y: top;
background-position-x: right;
height: 100%; 

}
.font{
  font-family: 'League Spartan', sans-serif;
  color:#282c34;
}
.firstStep {
  width: 100%;
  min-height: 500px;
  display: flex;
  flex-direction: row;
}
.steps{
  width: 45%;
  height: 500px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.steps2{
  width: 45%;
  height: 500px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.title{
  font-size: 50px;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #313131;
}

h1,
h2,
h3,
h4 {
  font-family: 'League Spartan', sans-serif;
}

.App {
  color: white;
}

body {
  background-color: #282c34;
  color: white;
  
}
#localVideo{
  
    z-index: 99999;
    position: absolute;
    background-color: black;

}

.modalmask {
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0px;
  left: 0px;
  bottom: 0px;
  right: 0px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #282c34a1;
}

.modal {
  border-top: 5px solid #76a929;
  position: relative;
  border-radius: 25px;
  padding: 20px;
  -webkit-box-shadow: 1px 1px 29px 0px rgba(0, 0, 0, 0.26);
  -moz-box-shadow: 1px 1px 29px 0px rgba(0, 0, 0, 0.26);
  box-shadow: 1px 1px 29px 0px rgba(0, 0, 0, 0.26);
  background-color: #282c34;
  width: 550px;
  height: calc(100% - 100px);
  display: inline;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

* {
  margin: 0px;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  color: white;
}

.nav-link:hover {
  color: #75A929;
}

a {
  color: #75A929;
  text-decoration: none;
}

a:hover {
  color: #9bf01c;

}

.menu-all {
  width: 300px;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
}

.fadeIn {
  animation: 0.5s fadeIn forwards;
}

.fadeOut {
  animation: 0.5s fadeOut forwards;
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translate(0px, 50px);
    transform: scale(0.95);
  }

  to {
    opacity: 1;
    transform: translate(0px, 0px);
    transform: scale(1);
  }
}

@keyframes fadeOut {
  from {
    opacity: 1;
    transform: translate(0px, 0px);
  }

  to {
    transform: translate(-20px, 0);
    opacity: 0;
  }
}
@media only screen and (max-width: 600px) {
  .title{
    font-size: 25px;
  }
  .bg-img{
    background-image: url('./play.png');
    background-repeat: no-repeat;
    background-position-y: bottom;
    background-position-x: right; 
    background-size: 322px 295px;
    }
  .steps{
    width: 100%;
    height: 500px;
    display: flex;
   
    
    text-align: center;
  }
  .steps2{
    display:none;
  }
  .firstStep {
    width: 100%;
    min-height: 500px;
    display: flex;
    flex-direction: column;
  }
  .firstStep {
    width: 100%;
    min-height: 350px;
    display: flex;
    flex-direction: row;
  }
  .menu-all {
    width: 200px;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
  }
  
.modal {
  border-top: 5px solid #76a929;
  position: relative;
  border-radius: 0px;
  padding: 10px;
  -webkit-box-shadow: 1px 1px 29px 0px rgba(0, 0, 0, 0.26);
  -moz-box-shadow: 1px 1px 29px 0px rgba(0, 0, 0, 0.26);
  box-shadow: 1px 1px 29px 0px rgba(0, 0, 0, 0.26);
  background-color: #282c34;
  width: 100%;
  height: calc(100% - 5px);
  display: inline;
}

  
}